
$fontcolor_white: #fff;
$fontcolor_grey: #7a7a7a;
$fontcolor_dark: #272727;
$color-red: #f91e39;
$color-lightred: #ff3950;
$discount-green: #00920f;
$shopee-orange: #ee4d2d;




.ContactUsPage {
    background: linear-gradient(90deg, rgba(0, 0, 0, 0.75),rgba(0, 0, 0, 0.55)),  url('../images/bg5.png');
    background-repeat: no-repeat;
    background-position: bottom center; 
    background-size: cover;
}



.wrap-contus {
    width: 100%;
    height: auto;
}


.contus-container {
    width: 80%;
    max-width: 1600px;
    height: auto;
    margin: 0 auto;
    padding: 60px 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.contus-cont-1 {
    width: 52%;
}

.contus-cont-2 {
    width: 45%;
    height: auto;
    padding: 50px 60px;
    border-radius: 8px;
    box-shadow: 0 5px 20px 0 rgba(0, 0, 0, 0.1);
}

.contus-cont-1 > h2 {
    color: $color-red;
    font-size: 22px;
    font-weight: bold;
    line-height: 1.3;
    letter-spacing: normal;
    margin-bottom: 16px;
}

.contus-cont-1 > h3 {
    color: $fontcolor_dark;
    font-size: 35px;
    font-weight: normal !important;
    line-height: 1.3;
    letter-spacing: normal;
    margin-bottom: 30px;
}

.contus-cont-1 > .contus-cont-1-p1 {
    color: $fontcolor_grey;
    font-size: 16px;
    font-weight: normal;
    line-height: 1.5;
    letter-spacing: normal;
}

.contus-cont-1 > .contus-cont-1-p2 {
    color: #000;
    font-size: 16px;
    font-weight: bold;
    line-height: 1.5;
    letter-spacing: normal;
    margin-bottom: 45px;
}

.contus-cont-1-grid {
    width: 100%;
    height: auto;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
    margin-bottom: 50px;

}

.contus-1-grid-item {
    background-color: #fff2f4;
    padding: 20px 24px;
    border-radius: 5px;
}

.contus-1-grid-head {
    display: flex;
    align-items: center;
    margin-bottom: 16px;
}

.contus-1-grid-head > h4 {
    font-size: 16px;
}

.contus-icon-1 {
    color: $color-red;
    font-size: 24px;
    margin-right: 20px;
}

.contus-icon-2 {
    color: $color-red;
    font-size: 20px;
    margin-right: 20px;
}

.contus-1-grid-body {  
    color: #54595f;
    font-size: 15px;
}

.contus-cont-1-socials {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.contus-cont-1-socials > div > a {
    text-decoration: none;
}

.contus-socials-icon-1 {
    color: $fontcolor_white;
    background-color: $color-red;
    margin-left:20px;
    padding: 9px 10px 7px 10px;
    font-size: 18px;
    border-radius: 5px;
    transition: all 0.3s ease-in-out;
}

.contus-socials-icon-1:hover {
    background-color: $color-lightred;
}



// form
.contus-cont-2-form-container {
    width: 100%;
    height: auto;
}

.contus-cont-2-form > div > label {
    color: $fontcolor_dark;
    font-size: 14px;
    font-weight: normal;
    line-height: 1.4;
    letter-spacing: normal;
    margin-bottom: 5px;
}

.contus-cont-2-form > div {
    width: 100%;
    margin-bottom: 20px;
}

.contus-cont-2-form > div > label > span {
    color: $color-red;
}

.contus-cont-2-form > div > input {
    color: $fontcolor_dark;
    background-color: #fff;
    width: 100%;
    padding: 12px ;
    font-size: 14px;
    font-weight: bold;
    border: solid 1px #acacac;
    border-radius: 5px;
    outline: 0;
}

.contus-cont-2-form > div > textarea {
    color: $fontcolor_dark;
    background-color: #fff;
    width: 100%;
    height: 200px;
    margin-bottom: 20px;
    padding: 10px 12px;
    font-size: 14px;
    font-weight: bold;
    border: solid 1px #acacac;
    border-radius: 5px;
    outline: 0;
    resize: none;
}

.contus-cont-2-submit {
    color: $fontcolor_white;
    background-color: $color-red;
    padding: 15px 30px;
    font-size: 14px;  
    line-height: 1.5;
    border: none;
    border-radius: 5px;
    transition: all 0.3s ease-in-out;
    text-transform: uppercase;
}
.contus-cont-2-submit:hover {
    background-color: $color-lightred;
}


@media screen and (max-width: 1200px) {
    .contus-container {
        width: 90%;
        padding: 0 0 60px;
        flex-direction: column;
    }

    .contus-cont-1 {
        width: 100%;
        margin-bottom: 60px;
    }

    .contus-cont-2 {
        width: 70%;
    }
}


@media screen and (max-width: 576px) {

    .contus-cont-1-grid {
        grid-template-columns: 1fr;
        margin-bottom: 30px;
    }

    .contus-cont-1-socials {
        flex-direction: column;
        align-items:flex-start;
    }

    .contus-cont-1-socials > div > p {
        margin-bottom: 20px;
    }

    .contus-socials-icon-1 {
        margin: 0 20px 0 0;
    }

    .contus-cont-2 {
        width: 100%;
        padding: 40px;
    }

}














