
.wrap-footer {
    width: 100%;
    height: auto;
    text-align: center;
    margin: 0 ;
    background-color: #000;
}


.footer-content-1 {
    width: 80%;
    max-width: 1600px;
    height: auto;
    margin: 0 auto;
    padding: 60px 0 12px;  
    background-color: #000;
}

.footer-content-1 > img {
    width: 300px;
    height: auto;
    margin-bottom: 40px;
}

.footer-cont-1-item-01 {
    margin-bottom: 16px;
}

.footer-cont-1-item-01 > a, .footer-cont-1-item-01 > a:hover {
    text-decoration: none !important;
}

.footer-socials-icon {
    color: #fff;
    font-size: 32px;
    margin: 0 20px;
    transition: all .3s ease-in-out;
}
.footer-socials-icon:hover{
    color: #ff0000;
    text-decoration: none;
}

.footer-cont-1-item-01 {
    margin-bottom: 28px;
}

.footer-cont-1-item-01 > h3 {
    font-size: 16px;
    font-weight: bold;
    line-height: 1.56;
    text-align: center;
    color: #f8f8f8;
    margin-bottom: 16px;
}

.footer-cont-1-item-01 > a {
    font-size: 16px;
    font-weight: normal;
    line-height: 1.56;
    text-align: center;
    color: #f8f8f8;
    text-decoration: none;
}
.footer-cont-1-item-01 > a:hover {
    text-decoration: underline;
}

.footer-cont-1-item-02 > div {
    margin-bottom: 28px;
}

.footer-cont-1-item-02 > div > a {
    color: #fff;
    font-size: 14px;
    font-weight: 500;
    line-height: 1.5;
    text-align: center;
    text-transform: uppercase;
    text-decoration: none;
    margin: 0 20px;
}

.footer-content-2 {
    width: 80%;
    max-width: 1600px;
    height: auto;
    margin: 0 auto;
    padding: 24px 0;
    border-top: 1px solid #7a7a7a;
}

.footer-content-2 > p {
    font-size: 14px;
    font-weight: normal;
    text-align: center;
    color: #acacac;
}

.footer-cont-1-contacts {
    display: flex;
    align-items: center;
    justify-content: center;
}

.footer-cont-1-contacts > div {
    margin: 0 50px;
    display: flex;
    align-items: center;
}

.footer-cont-1-contacts > div > a {
    color: #fff;
    font-size: 14px;
    font-weight: normal;
    line-height: 1.4;
    letter-spacing: normal;
    text-decoration: none;
}

.footer-contacts-icon-1 {
    color: #fff;
    font-size: 20px;
    margin-right: 10px;
}

.footer-cont-1-maps {
    display: flex;
    justify-content: center;
}
/* .footer-cont-1-maps .dropdown-basic */
.footer-dropdown-header {
    height: auto !important;
    color: #fff;
    padding: 0 !important;
    margin: 0 30px 4px 0 !important;
    font-size: 14px !important;
    font-weight: normal;
    line-height: 1.4;
    letter-spacing: normal;
    text-decoration: none;
}



@media screen and (max-width: 1200px) {
    .footer-cont-1-item-02 > .footer-cont-1-maps {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;
        gap: 20px;
    }

    .footer-cont-1-item-02 > div > a {
        text-align: left;
        margin: 0;
    }
}


@media screen and (max-width: 576px) {
    .footer-content-1 > img {
        width: 150px;
    }

    .footer-cont-1-item-01 > h3 {
        font-size: 14px;
        margin-bottom: 8px;
    }

    .footer-cont-1-item-01 > a {
        font-size: 14px;
        font-weight: 300;
    }

    .footer-cont-1-item-02 > p {
        width: 50%;
        margin: 0 auto;
        font-size: 12px;
    }

    .footer-content-2 > p {
        font-size: 12px;
    }


    .footer-cont-1-contacts {
        flex-direction: column;
        margin-bottom: 0;
    }

    .footer-cont-1-contacts > div {
        margin-bottom: 40px;
    }

    .footer-cont-1-item-02 > .footer-cont-1-maps {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 40px 0px;
    }
    .footer-cont-1-item-02 > div > a {
        text-align: center;
        line-height: 1.5;
    }
}