$fontcolor_white: #fff;
$fontcolor_grey: #7a7a7a;
$fontcolor_dark: #272727;
$color-red: #f91e39;
$color-lightred: #ff3950;
$discount-green: #00920f;
$shopee-orange: #ee4d2d;




.BrandsPage {
    background: linear-gradient(90deg, rgba(0, 0, 0, 0.75),rgba(0, 0, 0, 0.55)),  url('../images/bg6.png');
    background-repeat: no-repeat;
    background-position: bottom center; 
    background-size: cover;
}

.wrap-brands {
    padding-bottom: 120px;
}

.brands-p-header {
    width: 80%;
    max-width: 1600px;
    height: auto;
    margin: 0 auto 90px;
}

.brands-p-header > h2 {
    color: $fontcolor_dark;
    font-size: 35px;
    font-weight: bold;
    line-height: 1.3;
    text-align: left;
    margin-bottom: 16px;
}

.brands-p-header > p {
    color: $fontcolor_dark;
    font-size: 16px;
    font-weight: normal;
    line-height: 1.5;
}



@media screen and (max-width: 1200px) {
        
    .wrap-brands {
        padding-bottom: 60px;
    }

    .brands-p-header {
        width: 90%;
        margin-bottom: 30px;
    }
}



@media screen and (max-width: 576px) {
        
    .brands-item-1 {
        grid-template-columns: 1fr;
        gap: 60px;
        padding: 0 60px;
    }

}













