
$fontcolor_white: #fff;
$fontcolor_grey: #7a7a7a;
$fontcolor_dark: #272727;
$color-red: #f91e39;
$color-lightred: #ff3950;
$discount-green: #00920f;
$shopee-orange: #ee4d2d;

$border-radius-5: 5px;
$border-radius-10: 10px;
$box-shadow-1: rgba(0, 0, 0, 0.05) 0px 3px 12px;
$box-shadow-2: rgba(0, 0, 0, 0.1) 0px 4px 12px;



.wrap-hero {
    width: 100%;
    height: 100vh;
    color: $fontcolor_white;
    background: linear-gradient(90deg, rgba(0, 0, 0, 0.9),rgba(0, 0, 0, 0.2)),  url('../images/bg1.png');
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: top center; 
    background-size: cover;
    border-radius: 0% 0% 50% 50% / 88% 88% 12% 12%;
}

.hero-container {
    width: 80%;
    max-width: 1600px;
    height: 80%;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    flex-direction: column;
}

.hero-heading {
    width: 50%;
    margin-bottom: 30px;
}

.hero-heading > h1 {
    color: $fontcolor_white;
    font-size: 60px;
    line-height: 1.3;
}

.hero-desc {
    width: 40%;
    margin-bottom: 40px;
}

.hero-desc > p {
    width: 80%;
    font-size: 16px;
    line-height: 1.5;
    margin-bottom: 20px;  
}

.hero-p-line {
    width: 120px;
    height: 5px;
    background-color: $color-red;
}

.hero-contactus-btn > button {
    color: $fontcolor_white;
    background: $color-red;
    font-size: 14px;
    font-weight: bold;
    padding: 15px ;
    line-height: 1.5;
    text-transform: uppercase;
    border: none;
    border-radius: $border-radius-5;
    transition: all 0.3s ease-in-out;
}
.hero-contactus-btn > button:hover {
    background: $color-lightred;
}

.hero-container-2 {
    width: 80%;
    max-width: 1600px;
    margin: 70px auto 0;
    display: grid;
    justify-content: space-between;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 30px;
}

.hero-cont-1 {
    background-color: #fff;
    padding: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: $border-radius-10;
    box-shadow: 0 4px 32px 0 rgba(0, 0, 0, 0.25);
}

.hero-cont-1-img {
    width: 30%;
    text-align: center;
}

.land_hero-icon-1 {
    color: $color-red;
    font-size: 60px;
}

.land_hero-icon-2 {
    color: $color-red;
    font-size: 50px;
}

.hero-cont-1-desc {
    width: 70%;
}

.hero-cont-1-desc > h2 {
    color: $fontcolor_dark;
    font-size: 24px;
    margin-bottom: 6px;  
    line-height: 1.3;
}

.hero-cont-1-desc > p {
    color: $fontcolor_grey;
    font-size: 15px;
}




// Landing Content 
.wrap-content {
    width: 100%;
    height: auto;
    padding: 140px 0 60px;
}

.content-container {
    width: 100%;
    height: auto;
    margin-bottom: 100px;
}

.cont-header {
    width: 80%;
    max-width: 1600px;
    height: auto;
    margin: 0 auto 60px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.cont-header > h2 {
    font-size: 22px;
    font-weight: bold;
    text-align: center;
    text-transform: uppercase;
    line-height: 1.3;
    letter-spacing: normal;
}

.cont-header > .header-line {
    width: 40px;
    height: 4px;
    background-color: $color-red;
    margin: 0 10px;
}


// About Us 
.abUs-content {
    width: 80%;
    max-width: 1600px;
    height: auto;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
}

.abUs-item-1 {
    width: 35%;
}
.abUs-item-1 > img {
    width: 100%;
    border-radius: $border-radius-5;
}

.abUs-item-2 {
    width: 60%;
}
.abUs-item-2 > img {
    width: 200px;
    height: auto;
    margin-bottom: 32px;
}
.abUs-item-2 > p {
    color: $fontcolor_grey;
    font-size: 16px;
    margin-bottom: 32px;
}
.abUs-item-2 > a > button {
    color: $fontcolor_white;
    background-color: #000;
    font-size: 14px;
    text-transform: uppercase;
    padding: 12px 32px;
    border-radius: $border-radius-5;
    border: 2px solid #000;
    transition: all 0.3s ease-in-out;
}
.abUs-item-2 > a > button:hover {
    background-color: $fontcolor_dark;
}



// Brands
.brands-content {
    width: 80%;
    max-width: 1600px;
    height: auto;
    margin: 0 auto;
}

.brands-item-1 {
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    justify-content: center;
    gap: 120px;
}

.brands-item-1 > img, .brands-item-1 > a > img {
    width: 100%;
}


// Products
.prod-showcase {
    width: 80%;
    max-width: 1600px;
    height: auto;
    margin: 0 auto 90px;
}

.prod-content {
    width: 100%;
    height: auto;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    justify-content: center;
    gap: 30px;
}

.prod-subheading {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 30px;
}

.prod-subhead-item-1 {
    display: flex;
    align-items: center;
}
.prod-subhead-item-1 > h3 {
    font-size: 36px;
    font-weight: bold;
}

.prod-subhead-item-2 > a {
    color: #000;
    font-size: 16px;
    font-weight: bold;
    text-decoration: none;
    text-transform: uppercase;
}

.prod-subhead-icon-1 {
    font-size: 28px;
    color: $color-red;
    margin-right: 20px;
}
.prod-subhead-icon-2 {
    font-size: 24px;
    color: $color-red;
    margin-bottom: 3px;
}


// ProductCardsItem
.prod-cards {
    background-color: #fff;
    padding: 20px;
    border-radius: $border-radius-5;
    box-shadow: 0 4px 14px 0 rgba(0, 0, 0, 0.25);
}

.prod-cards > img {
    width: 100%;
    margin-bottom: 16px;
}

.prod-cards > h4 {              // title
    color: $fontcolor_dark;
    font-size: 16px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    margin-bottom: 6px;
}
.prod-cards > p {               // new price
    color: $color-red;
    font-size: 20px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.3;
    letter-spacing: normal;
}

.prod-discprice {               // discount price 
    display: flex;
    align-items: center;
    margin-bottom: 6px;
}
.prod-oldPrice {                // old price
    color: $fontcolor_grey;
    font-size: 14px;
    font-weight: normal;
    line-height: 1.86;
    letter-spacing: normal;
    text-decoration: line-through;
    margin-right: 10px;
}
.prod-discountPerc {            // discount percentage
    color: $discount-green;
    font-size: 14px;
    font-weight: bold;
    line-height: 1.86;
    letter-spacing: normal;
    text-align: center;
} 

.prod_btn-container {
    display: flex;
    flex-direction: column;
    text-align: center;
}
.prod-moredetails > button {
    color: $fontcolor_white;
    background-color: #000;
    font-size: 14px;
    text-transform: uppercase;
    padding: 12px 32px;
    border-radius: $border-radius-5;
    border: 2px solid #000;
    transition: all 0.3s ease-in-out;
    margin-bottom: 20px;
}
.prod-moredetails > button:hover {
    background-color: $fontcolor_dark;
}
.prod-shopeelink {
    color: $shopee-orange;
    font-size: 14px;
    font-weight: bold;
    line-height: 1.5;
    text-transform: uppercase;
    text-decoration: underline;
}
.prod-shopeelink:hover {
    color: $shopee-orange;
}
.prod-shopeelink > .shopee-icon-1 {
    font-size: 15px;
    color: $fontcolor_white;
    background-color: $shopee-orange;
    padding: 4px 6px 2px;
    border-radius: 50px;
    margin-right: 8px;
}
.prod-shopeelink > .shopee-icon-2 {
    font-size: 14px;
} 






@media screen and (min-width: 1901px) {

    .hero-container-2 {
        width: 80%;
        max-width: 1600px;
        margin: 90px auto 0;
        display: grid;
        justify-content: space-between;
        grid-template-columns: 1fr 1fr 1fr;
        gap: 30px;
    }
    
    .prod-content {
        width: 100%;
        height: auto;
        margin: 0 auto;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;
        justify-content: center;
        gap: 48px;
    }
}



@media screen and (max-width: 1200px) {
    .wrap-hero {
        height: auto;
        background-attachment: initial;
    }

    .hero-container {
        width: 90%;
        height: 500px;
    }
    .hero-heading {
        width: 60%;
    }
    .hero-heading > h1 {
        font-size: 42px;
    }
    .hero-desc {
        width: 70%;
    }

    .hero-container-2 {
        width: 90%;
        gap: 20px;
    }
    .hero-cont-1 {
        padding: 10px;
    }
    .hero-cont-1-img {
        width: 25%;
    }
    .hero-cont-1-desc {
        width: 75%;
    }

    .land_hero-icon-1 {
        font-size: 36px;
    }
    .land_hero-icon-2 {
        font-size: 30px;
    }



    // content
    .wrap-content {
        padding: 90px 0 30px;
    }



    // aboutus
    .abUs-content {
        width: 90%;
    }


    // brands 
    .brands-content {
        width: 90%;
    }
    .brands-item-1 {
        gap: 60px;
    }


    .prod-showcase {
        width: 90%;
    }
    .prod-content {
        grid-template-columns: 1fr 1fr 1fr;
        gap: 30px ;
    }
    
}


@media screen and (max-width: 576px) {
    .wrap-hero {
        border-radius: 0% 0% 90% 90% / 90% 90% 10% 10%;
    }

    .hero-heading {
        width: 100%;
    }

    .hero-desc {
        width: 90%;
    }

    .hero-container-2 {
        width: 90%;
        grid-template-columns: 1fr;
    }


    // content
    .wrap-content {
        padding-bottom: 0;
    }
    .prod-content {
        grid-template-columns: 1fr;
        gap: 20px;
    }

    .cont-header {
        margin-bottom: 40px;
    }

    .prod-subhead-item-1 > h3 {
        font-size: 28px;
    }

    .content-container > .prod-showcase > .prod-content {
        width: 80%;
    }

    // aboutus
    .abUs-content {
        flex-direction: column;
    }

    .abUs-item-1 {
        width: 100%;
        margin-bottom: 30px;
    }
    .abUs-item-2 {
        width: 100%;
    }

    // brands
    .brands-item-1 {
        grid-template-columns: 1fr;
        gap: 10px 30px;
    }
}