
$fontcolor_white: #fff;
$fontcolor_grey: #7a7a7a;
$fontcolor_dark: #272727;
$color-red: #f91e39;
$color-lightred: #ff3950;
$discount-green: #00920f;
$shopee-orange: #ee4d2d;

$border-radius-5: 5px;
$border-radius-10: 10px;

$hero-border-radius: 0% 0% 50% 50% / 88% 88% 12% 12%;
$box-shadow-1: rgba(0, 0, 0, 0.05) 0px 3px 12px;
$box-shadow-2: rgba(0, 0, 0, 0.1) 0px 4px 12px;



.wrap-pageHero {
    width: 100%;
    height: 320px;
    border-radius: $hero-border-radius;
    margin-bottom: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.wrap-pageHero > h1 {
    color: $fontcolor_white;
    font-size: 40px;
    margin-bottom: 30px;
}

.pageHero-breadcrumbs {
    display: flex;
    align-items: center;
    justify-content: center;
}

.pageHero-breadcrumbs > a, .pageHero-breadcrumbs > p {
    color: $fontcolor_white;
    font-size: 16px;
    font-weight: normal;
    text-decoration: none;
}

.pageHero-icon-1 {
    font-size: 24px;
    color: $color-red;
    margin: 0 20px;
}


// Hero background
.ProductPageHero {
    background: linear-gradient(90deg, rgba(0, 0, 0, 0.75),rgba(0, 0, 0, 0.55)),  url('../../images/bg6.png');
    background-repeat: no-repeat;
    background-position: bottom center; 
    background-size: cover;
}

.AllProductPage {
    background: linear-gradient(90deg, rgba(0, 0, 0, 0.75),rgba(0, 0, 0, 0.55)),  url('../../images/bg6.png');
    background-repeat: no-repeat;
    background-position: bottom center; 
    background-size: cover;
}





.wrap-allprod {
    width: 100%;
    height: auto;
    background-color: #fff;
}

.allprod-container {
    width: 80%;
    max-width: 1600px;
    height: auto;
    margin: 0 auto 90px;
}

.allprod-header {
    margin: 0 0 30px;
}
.allprod-header > h2 {
    color: #000;
    font-size: 30px;
    margin-bottom: 20px;
}
.allprod-header > p {
    color: #000;
    font-size: 16px;
}


.only-reg-prodPrice {
    margin-bottom: 20px;
}










@media screen and (max-width: 1200px) {
    
    .wrap-pageHero {
        margin-bottom: 40px;
    }

    .allprod-container {
        width: 90%;
    }
}



@media screen and (max-width: 576px) {
    
    .wrap-pageHero {
        margin-bottom: 30px;
    }


    .allprod-container > .prod-content {
        width: 80%;
    }

    // related products
    .proddetails-cont-3-item {
        width: 90%;
        margin: 0 auto;
    }
}



