
$fontcolor_white: #fff;
$fontcolor_grey: #7a7a7a;
$fontcolor_dark: #272727;
$color-red: #f91e39;
$color-lightred: #ff3950;
$discount-green: #00920f;
$shopee-orange: #ee4d2d;

$border-radius-5: 5px;
$border-radius-10: 10px;


.prod-item-01 {
    width: 100%;
}

.prod-item-01 > img {
    width: 100%;
}

.wrap-prod-details {
    width: 100%;
    height: auto;
    padding: 0 0 60px;
}


.proddetails-breadcrumbs {
    width: 80%;
    max-width: 1600px;
    height: auto;
    margin: 0 auto;
    padding: 32px 0;
    display: flex;
    align-items: center;
}

.proddetails-breadcrumbs-mobile {
    display: none;
}

.proddetails-breadcrumbs > p {
    color: #767676;
    margin: 0;
    font-size: 14px;
    font-weight: normal;
    line-height: 1.58;
    letter-spacing: normal;
}

.proddetails-breadcrumbs > span {
    color: #767676;
    margin: 0 10px;
}


.proddetails-container {
    width: 100%;
}

.proddetails-content-1 {
    width: 80%;
    max-width: 1600px;
    height: auto;
    margin: 0 auto 70px;
    display: grid;
    grid-template-columns: 1fr 2fr;
    gap: 40px;
}

.proddetails-cont-1-item-1 > img {
    width: 100%;
    cursor: pointer;
}


.proddetails-cont-1-item-2 > h1 {
    color: $fontcolor_dark;
    font-size: 24px;
    font-weight: bold;
    line-height: 1.42;
    letter-spacing: normal;
    margin-bottom: 24px;
}

.proddetails-cont-1-item-2 > p {
    color: $color-red;
    font-size: 32px;
    font-weight: bold;
    line-height: 0.81;
    letter-spacing: normal;
    margin-bottom: 12px;
}

.proddetails-discprice {
    display: flex;
    align-items: center;
    margin-bottom: 24px;
}

.proddetails-discprice > .proddetails-oldPrice {
    color: $fontcolor_grey;
    font-size: 16px;
    font-weight: normal;
    line-height: 1.86;
    letter-spacing: normal;
    text-decoration: line-through;
    margin-right: 10px;
}

.proddetails-discprice > .proddetails-discountPerc {
    color: $discount-green;
    font-size: 16px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.86;
    letter-spacing: normal;
    text-align: center;
}

.proddetails-shortdesc {
    margin-bottom: 24px;
}

.proddetails-shortdesc > p {
    color: #333;
    font-size: 16px;
    font-weight: normal;
    line-height: 1.5;
    letter-spacing: normal;
    text-align: left;
}

.proddetails-shortdesc > pre {
    height: 105px;
    overflow: hidden;
}

.proddetails_btn-container > a {
    text-decoration: none;
}

.proddetails_btn-container > a > button {
    color: $fontcolor_white;
    background-color: $color-red;
    padding: 14px 24px;
    font-size: 14px;
    font-weight: bold;
    display: flex;
    align-items: center;
    border: none;
    border-radius: 5px;
    text-transform: uppercase;
    transition: all 0.3s ease-in-out;
}

.proddetails_btn-container > a > button:hover {
    background-color: $color-lightred;
}
.proddetails_btn-container > a > button > span {
    font-size: 18px;
    margin-right: 12px;
}


.proddetails-content-2 {
    width: 80%;
    max-width: 1600px;
    height: auto;
    margin: 0 auto 70px;
}

.proddetails-cont-2-item-1-head {
    display: flex;
    margin-bottom: 20px;
}

.proddetails-cont-2-item-1-head > span {
    width: 100%;
    border-bottom: 1px solid #d3ced2;
}

.proddetails-cont-2-item-1-head > button {
    width: auto;
    color: $fontcolor_dark;
    background-color: $fontcolor_white;
    padding: 12px 20px;
    border: 1px solid #d3ced2;
    border-bottom: 1px solid #fff;
    cursor: initial;
}

.proddetails-cont-2-item-1-desc {
    width: 100%;
}

.proddetails-cont-2-item-1-desc > h2 {
    color: $fontcolor_dark;
    font-size: 35px;
    font-weight: bold;
    line-height: 1.3;
    letter-spacing: normal;
    margin-bottom: 20px;
}

// .proddetails-cont-2-item-1-desc > p {
//     color: #333;
//     font-size: 16px;
// }
.proddetails-cont-2-item-1-desc > p > pre {
    width: 100%;
    color: #333;
    padding-bottom: 20px;
    margin-bottom: 0;
    font-size: 16px;
    font-weight: normal;
    line-height: 1.5;
    letter-spacing: normal;
    text-align: left;
}


.proddetails-content-3 {
    width: 80%;
    max-width: 1600px;
    height: auto;
    margin: 0 auto;
}

.proddetails-cont-3-head > h2 {
    color: $fontcolor_dark;
    font-size: 35px;
    font-weight: bold;
    line-height: 1.3;
    letter-spacing: normal;
    margin-bottom: 20px;
}


// modal
.modal-backdrop.show {
    opacity: 0.8;
}
.modal {
    display: flex !important;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}
.modal-dialog {
    max-width: 40%;
    cursor: initial;
}
.modal-body > img {
    width: 100%;
}





@media screen and (max-width: 1200px) {

    .proddetails-breadcrumbs {
        display: none;
    }
    
    .proddetails-breadcrumbs-mobile {
        width: 90%;
        margin: 0 auto;
        padding: 32px 0;
        display: block;
        flex-direction: initial;
    }

    .proddetails-breadcrumbs-mobile > p {
        color: #767676;
        margin: 0;
        font-size: 14px;
        font-weight: normal;
        line-height: 1.58;
        letter-spacing: normal;
    }
    .proddetails-breadcrumbs-mobile > p > span {
        margin: 0 10px;
    }

    .proddetails-content-1 {
        width: 90%;
        gap: 20px;
    }


    .proddetails-content-2 {
        width: 90%;
    }

    .proddetails-cont-2-item-1-desc > h2, .proddetails-cont-3-head > h2 {
        font-size: 24px;
    }

    .proddetails-content-3 {
        width: 90%;
    }


    // modal
    .modal-dialog {
        max-width: 60%;
    }
}


@media screen and (max-width: 576px) {

    .proddetails-content-1 {
        grid-template-columns: 1fr;
    }

    .proddetails-cont-1-item-2 > h1 {
        font-size: 20px;
        line-height: 1.7;
    }

    .proddetails-cont-1-item-2 > p {
        font-size: 32px;
        line-height: 0.81;
    }

    .proddetails-shortdesc > p {
        line-height: 1.5;
    }
    
    .proddetails-shortdesc > pre {
        height: 105px;
        overflow: hidden;
    }
    
    .proddetails-cont-2-item-1-desc > h2, .proddetails-cont-3-head > h2 {
        font-size: 24px;
    }

    
    // modal
    .modal-dialog {
        max-width: 90%;
    }
}















