
$fontcolor_white: #fff;
$fontcolor_grey: #7a7a7a;
$fontcolor_dark: #272727;
$color-red: #ff0000;
$discount-green: #00920f;
$shopee-orange: #ee4d2d;

$border-radius-5: 5px;
$border-radius-10: 10px;
$box-shadow-1: rgba(0, 0, 0, 0.05) 0px 3px 12px;
$box-shadow-2: rgba(0, 0, 0, 0.1) 0px 4px 12px;


.AboutUsPage {
    background: linear-gradient(90deg, rgba(0, 0, 0, 0.75),rgba(0, 0, 0, 0.55)),  url('../images/bg1.png');
    background-repeat: no-repeat;
    background-position: bottom center; 
    background-size: cover;
}

.wrap-aboutus {
    width: 100%;
    height: auto;
}


.aboutus-container {
    width: 80%;
    max-width: 1600px;
    height: auto;
    margin: 0 auto;
    padding: 60px 0;
    display: flex;
    justify-content: space-between;
}

.aboutus-cont-1 {
    width: 48%;
}

.aboutus-cont-1-01 > img {
    width: 100%;
    border-radius: $border-radius-5;
}

.aboutus-cont-1-img-1 {
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
    margin-bottom: 20px;
}

.aboutus-cont-1-img-1 > div {
    width: 100%;
    background: #f0f0f0;
    padding: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    border-radius: $border-radius-5;
}

.aboutus-cont-1-img-1 > img {
    width: 100%;
    border-radius: $border-radius-5;
}

.aboutus-cont-1-p-01 {
    font-size: 36px;
    display: flex;
    align-items: center;
}

.aboutus-cont-1-p-01 > span {
    color: $color-red;
    font-size: 24px;
    margin-left: 8px;
}

.aboutus-cont-1-p-02 {
    color: $fontcolor_grey;
}

.aboutus-cont-2 {
    width: 48%;
}

.aboutus-cont-2 > h2 {
    color: $color-red;
    font-size: 28px;
    margin-bottom: 30px;
}

.aboutus-cont-2 > h3 {
    color: $fontcolor_dark;
    font-size: 40px;
    margin-bottom: 30px;
}

.aboutus-cont-2 > p {
    color: $fontcolor_grey;
    font-size: 16px;
    font-weight: normal;
    margin-bottom: 30px;
}





@media screen and (max-width: 1200px) {
    .aboutus-container {
        width: 90%;
        flex-direction: column;
        padding: 30px 0 90px;
    }

    .aboutus-cont-1 {
        width: 100%;
        margin-bottom: 60px;
    }

    .aboutus-cont-2 {
        width: 100%;
    }

    
    .aboutus-cont-2 > h3 {
        font-size: 36px;
    }
}


@media screen and (max-width: 576px) {
    .aboutus-container {
        padding: 0 0 60px;
    }

    .aboutus-cont-1 {
        width: 100%;
    }

    .aboutus-cont-2 {
        width: 100%;
        margin-bottom: 10px;
    }

    .aboutus-cont-2 > h2 {
        font-size: 22px;
    }

    .aboutus-cont-2 > h3 {
        font-size: 30px;
    }

    .aboutus-cont-1-img-1 > div {
        padding: 20px;
    }

    .aboutus-cont-1-img-1 {
        grid-template-columns: 1fr;
    }
}










